/**
 * User Manager
 * backend/apiÏóêÏÑú ÏÇ¨Ïö©ÌïòÎäî UserManager ÎûòÌçº
 * 
 * üîí Î≥¥Ïïà Í∞ïÌôî: bcryptÎ•º ÏÇ¨Ïö©Ìïú ÎπÑÎ∞ÄÎ≤àÌò∏ Ìï¥Ïã± Ï†ÅÏö©
 */

import * as bcrypt from 'bcryptjs';

// ÎπÑÎ∞ÄÎ≤àÌò∏ Ìï¥Ïã± ÎùºÏö¥Îìú (ÎÜíÏùÑÏàòÎ°ù ÏïàÏ†ÑÌïòÏßÄÎßå ÎäêÎ¶º)
const BCRYPT_ROUNDS = 12;

export type UserRole = 'admin' | 'user' | 'guest';

// MCP ÎèÑÍµ¨ Ï†ëÍ∑º Îì±Í∏â
export type UserTier = 'free' | 'pro' | 'enterprise';

export interface PublicUser {
    id: number;
    email: string;
    role: UserRole;
    tier: UserTier;
    created_at: string;
    last_login?: string;
    is_active: boolean;
}

export interface CreateUserInput {
    email: string;
    password: string;
    role?: UserRole;
    tier?: UserTier;
}

/**
 * Ïù∏Î©îÎ™®Î¶¨ UserManager
 */
class UserManagerImpl {
    private users: Map<number, { id: number; email: string; password: string; role: UserRole; tier: UserTier; is_active: boolean; created_at: string; last_login?: string }> = new Map();
    private nextId = 1;

    constructor() {
        // üîí Î≥¥Ïïà Í∞ïÌôî: ÌôòÍ≤ΩÎ≥ÄÏàò ÌïÑÏàòÌôî, Í∏∞Î≥∏ ÎπÑÎ∞ÄÎ≤àÌò∏ Ï†úÍ±∞
        const adminPassword = process.env.ADMIN_PASSWORD;
        if (!adminPassword) {
            console.warn('[UserManager] ‚ö†Ô∏è ADMIN_PASSWORD ÌôòÍ≤ΩÎ≥ÄÏàòÍ∞Ä ÏÑ§Ï†ïÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§!');
            console.warn('[UserManager] Í∏∞Î≥∏ Í¥ÄÎ¶¨Ïûê Í≥ÑÏ†ïÏù¥ ÏÉùÏÑ±ÎêòÏßÄ ÏïäÏäµÎãàÎã§. .env ÌååÏùºÏóê ADMIN_PASSWORDÎ•º ÏÑ§Ï†ïÌïòÏÑ∏Ïöî.');
            if (process.env.NODE_ENV === 'production') {
                throw new Error('[UserManager] ÌîÑÎ°úÎçïÏÖò ÌôòÍ≤ΩÏóêÏÑúÎäî ADMIN_PASSWORD ÌôòÍ≤ΩÎ≥ÄÏàòÍ∞Ä ÌïÑÏàòÏûÖÎãàÎã§!');
            }
            // Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑúÎßå ÏûÑÏãú ÎπÑÎ∞ÄÎ≤àÌò∏ ÏÉùÏÑ± (ÎûúÎç§)
            const tempPassword = require('crypto').randomBytes(16).toString('hex');
            console.warn(`[UserManager] Í∞úÎ∞ú ÌôòÍ≤Ω ÏûÑÏãú ÎπÑÎ∞ÄÎ≤àÌò∏: ${tempPassword}`);
            this.createUserSync({
                email: 'admin',
                password: tempPassword,
                role: 'admin'
            });
        } else {
            this.createUserSync({
                email: 'admin',
                password: adminPassword,
                role: 'admin'
            });
        }
    }

    // ÎèôÍ∏∞ Î≤ÑÏ†Ñ (ÏÉùÏÑ±ÏûêÏóêÏÑú ÏÇ¨Ïö©)
    private createUserSync(input: CreateUserInput): PublicUser | null {
        for (const user of this.users.values()) {
            if (user.email === input.email) return null;
        }
        const id = this.nextId++;
        const tier = input.tier || (input.role === 'admin' ? 'enterprise' : 'free');
        
        // üîí bcryptÎ°ú ÎπÑÎ∞ÄÎ≤àÌò∏ Ìï¥Ïã± (ÎèôÍ∏∞ Î≤ÑÏ†Ñ)
        const passwordHash = bcrypt.hashSync(input.password, BCRYPT_ROUNDS);
        
        const user = {
            id,
            email: input.email,
            password: passwordHash,
            role: input.role || 'user' as UserRole,
            tier,
            is_active: true,
            created_at: new Date().toISOString()
        };
        this.users.set(id, user);
        return this.toPublicUser(user);
    }

    createUser(input: CreateUserInput): PublicUser | null {
        for (const user of this.users.values()) {
            if (user.email === input.email) return null;
        }
        const id = this.nextId++;
        // admin Ïó≠Ìï†ÏùÄ ÏûêÎèôÏúºÎ°ú enterprise tier
        const tier = input.tier || (input.role === 'admin' ? 'enterprise' : 'free');
        
        // üîí bcryptÎ°ú ÎπÑÎ∞ÄÎ≤àÌò∏ Ìï¥Ïã±
        const passwordHash = bcrypt.hashSync(input.password, BCRYPT_ROUNDS);
        
        const user = {
            id,
            email: input.email,
            password: passwordHash,  // Ìï¥Ïã±Îêú ÎπÑÎ∞ÄÎ≤àÌò∏ Ï†ÄÏû•
            role: input.role || 'user' as UserRole,
            tier,
            is_active: true,
            created_at: new Date().toISOString()
        };
        this.users.set(id, user);
        return this.toPublicUser(user);
    }

    authenticate(email: string, password: string): PublicUser | null {
        for (const user of this.users.values()) {
            // üîí bcryptÎ°ú ÎπÑÎ∞ÄÎ≤àÌò∏ ÎπÑÍµê (Ìï¥Ïãú ÎπÑÍµê)
            if (user.email === email && bcrypt.compareSync(password, user.password) && user.is_active) {
                user.last_login = new Date().toISOString();
                return this.toPublicUser(user);
            }
        }
        return null;
    }

    getUserById(id: number): PublicUser | null {
        const user = this.users.get(id);
        return user ? this.toPublicUser(user) : null;
    }

    getUserByEmail(email: string): { id: number; email: string; password: string; role: UserRole } | null {
        for (const user of this.users.values()) {
            if (user.email === email) return user;
        }
        return null;
    }

    getAllUsers(options?: { page?: number; limit?: number; role?: UserRole; search?: string }): { users: PublicUser[]; total: number; page: number; limit: number } {
        let users = Array.from(this.users.values());
        if (options?.role) users = users.filter(u => u.role === options.role);
        if (options?.search) {
            const search = options.search.toLowerCase();
            users = users.filter(u => u.email.toLowerCase().includes(search));
        }
        const total = users.length;
        const page = options?.page || 1;
        const limit = options?.limit || 20;
        const start = (page - 1) * limit;
        return { users: users.slice(start, start + limit).map(u => this.toPublicUser(u)), total, page, limit };
    }

    changePassword(userId: number, newPassword: string): boolean {
        const user = this.users.get(userId);
        if (!user) return false;
        // üîí ÏÉà ÎπÑÎ∞ÄÎ≤àÌò∏ÎèÑ Ìï¥Ïã±ÌïòÏó¨ Ï†ÄÏû•
        user.password = bcrypt.hashSync(newPassword, BCRYPT_ROUNDS);
        return true;
    }

    changeRole(userId: number, newRole: UserRole): PublicUser | null {
        const user = this.users.get(userId);
        if (!user) return null;
        user.role = newRole;
        return this.toPublicUser(user);
    }

    updateUser(userId: number, updates: { email?: string; role?: UserRole; is_active?: boolean }): PublicUser | null {
        const user = this.users.get(userId);
        if (!user) return null;
        if (updates.email !== undefined) user.email = updates.email;
        if (updates.role !== undefined) user.role = updates.role;
        if (updates.is_active !== undefined) user.is_active = updates.is_active;
        return this.toPublicUser(user);
    }

    deleteUser(userId: number): boolean {
        const user = this.users.get(userId);
        if (!user) return false;
        if (user.role === 'admin') {
            const adminCount = Array.from(this.users.values()).filter(u => u.role === 'admin').length;
            if (adminCount <= 1) return false;
        }
        return this.users.delete(userId);
    }

    getStats() {
        const users = Array.from(this.users.values());
        return {
            totalUsers: users.length,
            activeUsers: users.filter(u => u.is_active).length,
            adminCount: users.filter(u => u.role === 'admin').length,
            userCount: users.filter(u => u.role === 'user').length,
            guestCount: users.filter(u => u.role === 'guest').length
        };
    }

    private toPublicUser(user: any): PublicUser {
        return { id: user.id, email: user.email, role: user.role, tier: user.tier, is_active: user.is_active, created_at: user.created_at, last_login: user.last_login };
    }

    // ÏÇ¨Ïö©Ïûê tier Î≥ÄÍ≤Ω
    changeTier(userId: number, newTier: UserTier): PublicUser | null {
        const user = this.users.get(userId);
        if (!user) return null;
        user.tier = newTier;
        return this.toPublicUser(user);
    }
}

let userManagerInstance: UserManagerImpl | null = null;

export function getUserManager(): UserManagerImpl {
    if (!userManagerInstance) userManagerInstance = new UserManagerImpl();
    return userManagerInstance;
}
